.catalog__wrapper {
  display: grid;
  grid-template-columns: 250px 1fr;
  row-gap: 60px;
  column-gap: 55px;
  padding: 80px 10px;

  @media (max-width: $tablet-width-max) {
    grid-template-columns: 1fr;
    row-gap: 0;
    column-gap: 0;
  }
}

.catalog__filters {
  @media (max-width: $tablet-width-max) {
    grid-column:  1 / -1;
    margin: 0 0 50px;
  }
}

.catalog__nav-buttons {
  display: flex;
  flex-direction: row;
  position: relative;
  justify-content: center;
  align-items: center;

  grid-column: 2 / -1;

  @media (max-width: $tablet-width-max) {
    grid-column: 1 / -1;
  }
}
